.toolbar {
	height: 56px;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: #703b09;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 20px;
	box-sizing: border-box;
	z-index: 90;
}

.toolbar nav {
	height: 100%;
}

.toolbarLogo {
  height: 80%;
}

@media (max-width: 499px) {
  .desktop {
    display: none;
  }
}