.drawer {
	position: fixed;
	width: 280px;
	max-width: 70%;
	height: 100%;
	left: 0;
	top: 0;
	z-index: 200;
	background-color: white;
	padding: 32px 16px;
	box-sizing: border-box;
	transition: transform 0.3s ease-out;
}

@media (min-width: 500px) {
	.drawer {
		display: none;
	}
}

.open {
	transform: translateX(0);
}

.close {
	transform: translateX(-100%);
}

.drawerLogo {
  height: 11%;
  margin-bottom: 32px;
  text-align: center;
}
